










import Vue from 'vue';

export default Vue.extend({
  name: 'WordCounter',
  data() {
    return {
      text: ''
    };
  },
  computed: {
    characterCount(): number {
      return this.text.length;
    },
    wordCount(): number {
      const text = this.text.trim();
      return text.length ? text.split(/[\s\r\n]+/).length : 0;
    }
  }
});



